import { Component, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { Ci18nMember } from 'src/app/shared/models/ci18n-member';


@Component({
  selector: 'app-team-others',
  templateUrl: './team-others.component.html',
  styleUrls: ['./team-others.component.scss']
})
export class TeamOthersComponent implements OnInit {

  public bError = false;

  // Team Carousel
  public members1: Ci18nMember[] = [{
      NAME: 'Loading',
      ROLE: 'Loading...',
      ORGANIZATION: 'Loading...',
      IMG: {
          ALT: '',
          SRC: ''
      }
  }];

  // Team Carousel
  public members2: Ci18nMember[] = [{
      NAME: 'Loading',
      ROLE: 'Loading...',
      ORGANIZATION: 'Loading...',
      IMG: {
          ALT: '',
          SRC: ''
      }
  }];

  constructor(public translate: TranslateService) { }

  ngOnInit() {
    this.translate.get('TEAM.OTHERS.MEMBERS1').subscribe((res: Ci18nMember[] | string) => {
      if (typeof(res) === 'string') {
          this.bError = true;
          this.members1 = [{
              NAME: 'An error occurred',
              ROLE: 'Cannot get translation',
              ORGANIZATION: 'Cannot get translation',
              IMG: {
                  ALT: '',
                  SRC: ''
              }
          }];
      } else {
          this.members1 = res as Ci18nMember[];
      }
  });

  this.translate.get('TEAM.OTHERS.MEMBERS2').subscribe((res: Ci18nMember[] | string) => {
    if (typeof(res) === 'string') {
        this.bError = true;
        this.members2 = [{
            NAME: 'An error occurred',
            ROLE: 'Cannot get translation',
            ORGANIZATION: 'Cannot get translation',
            IMG: {
                ALT: '',
                SRC: ''
            }
        }];
    } else {
        this.members2 = res as Ci18nMember[];
    }
});




  }

}
