import { Component, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { Ci18nMember } from 'src/app/shared/models/ci18n-member';

@Component({
  selector: 'app-team-main',
  templateUrl: './team-main.component.html',
  styleUrls: ['./team-main.component.scss']
})
export class TeamMainComponent implements OnInit {

  public bError = false;

  // Team Carousel
  public members: Ci18nMember[] = [{
      NAME: 'Loading',
      ROLE: 'Loading...',
      ORGANIZATION: 'Loading...',
      IMG: {
          ALT: '',
          SRC: ''
      }
  }];

  constructor(public translate: TranslateService) { }

  ngOnInit() {
    this.translate.get('TEAM.MAIN.MEMBERS').subscribe((res: Ci18nMember[] | string) => {
      if (typeof(res) === 'string') {
          this.bError = true;
          this.members = [{
              NAME: 'An error occurred',
              ROLE: 'Cannot get translation',
              ORGANIZATION: 'Cannot get translation',
              IMG: {
                  ALT: '',
                  SRC: ''
              }
          }];
      } else {
          this.members = res as Ci18nMember[];
      }
  });

  }

}
